.main {
  text-align: center;
  background-color: lightgrey;
  border-top: 2px solid lightpink; }
  .main .main__sayNumber {
    padding-top: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .main .main__sayNumber .main__say__title {
      text-align: center;
      font-size: 25x;
      width: 100%; }

.main__introduceNumber {
  padding-top: 30px; }
