      .main__introduceNumber .main__introduce__form .main__button__try:hover {
        border: 1px solid white; }
    .main__introduceNumber .main__introduce__form .main__button__reset {
      width: 150px;
      padding: 5px 0 5px 0;
      border: 1px solid lightpink;
      background-color: lightpink;
      border-radius: 5px; }
      .main__introduceNumber .main__introduce__form .main__button__reset:hover {
        border: 1px solid white; }
  .main__introduceNumber .main__counter__account {
