.header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px 0 15px;
  margin-top: 20px;
  margin-bottom: 18px;
  height: 85px;
  position: relative; }
  .header .header__title {
    height: 75px;
    font-weight: 600;
    font-size: 65px;
    margin: 0px;
    margin-right: 30px;
    font-family: 'La Belle Aurore', cursive; }
    @media all and (min-width: 768px) {
      .header .header__title {
        font-size: 80px; } }
  .header .pink {
    background-color: lightpink;
    position: absolute;
    z-index: -1;
    height: 10px;
    width: 100%; }
  .header .header__container {
    height: 120px;
    width: 110px;
    background-image: url("../images/adivina.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center; }

.footer {
  border-top: 2px solid lightpink;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 25px 20px 25px 20px; }
  @media all and (min-width: 768px) {
